.dialogs {
	display: grid;
	grid-template-columns: 2fr 10fr;
}

.dialogsItems {
	padding: 10px;
}
.dialogsItems .active {
	color: gold;
}

.messages {
	padding: 10px;
}

.messages .message {
	color: rgb(252, 248, 248);
}
.dialogAvatar{
	max-width: 40px;
	border-radius: 50%;
	overflow: hidden;
}